import React, {useState, useEffect} from "react";

import styles from './match.module.css';

import twitch from './img/twitch.png'
import bottom_open from './img/bottom_open.png'

import arrow_down from './img/arrow.png'
import arrow_up from './img/arrow_up.png'

import arrow_green from './img/up.png'
import arrow_red from './img/down.png'

import watch_green from './img/watch_green.png'
import watch_grey from './img/watch_grey.png'
import watch_active from './img/watch_active.png'

let Option = (e) => {
    let [isOpen, setIsOpen] = useState(false);
    useEffect(() => {
        if (isOpen) {
            setIsOpen(false)
        }
    }, [e.isOpen])
    return (
        <div className={styles.match__options}>
            {e.active_coef &&
            <div className={styles.option_win_line}
                 style={{left: e.active_coef === 'left' ? 0 : "", right: e.active_coef === 'right' ? 0 : "",
                     background: e.active_win ? "#399241" : "#A93B3D"}}>

            </div>
            }
            {e.active_coef &&
            <div className={styles.option_button}
                 style={{left: e.active_coef === 'left' ? 130 : "", right: e.active_coef === 'right' ? 130 : ""}}
                 onClick={() => setIsOpen(!isOpen)}
            >
                {e.active_win ? "+" : "-"}{e.weapons.length}

            </div>
            }
            {(e.active_coef && isOpen) &&
            // <div className={styles.option__list}>
            <div className={styles.option__list__wrapper} style={ e.active_coef === 'left' ? {left: 23} : {right: 23}}>
                {e.weapons.map(o => {
                    return <img src={require(`./img/weapons/${o}`)} alt="" className={styles.option__weapon}/>
                })}
            </div>
                // </div>
            }
            {e.active_coef &&
            <div className={styles.option__title}
                 style={{left: e.active_coef === 'left' ? 180 : "", right: e.active_coef === 'right' ? 180 : ""}}>
                <img src={require(`./img/weapons/${e.title_weapon}`)} alt=""/>
            </div>
            }
            <div className={styles.team_name} style={{justifyContent: "flex-end", marginRight: 16}}>{e.left_value}</div>
            <div className={styles.block__coef}
                 style={{background: e.active_coef === "left" ? e.active_win ? "#399241" : "#A93B3D" : ""}}>
                {e.left_coef}
            </div>
            <div className={styles.match__result}>
                {e.center_text}
            </div>
            <div className={styles.block__coef}
                 style={{background: e.active_coef === "right" ? e.active_win ? "#399241" : "#A93B3D" : ""}}>
                {e.right_coef}
            </div>
            <div className={styles.team_name} style={{justifyContent: "flex-start", marginLeft: 16}}>{e.right_value}</div>
        </div>
    )
}

let BroadCast = () => {
    let [activeLanguage, setActiveLanguage] = useState("RU")
    return (
        <div>
            <div className={styles.language__switch}>
                <span style={activeLanguage === "RU" ? {color: "#FFF", borderBottom: "2px solid #FCB90B"} : {}} onClick={() => setActiveLanguage("RU")}></span>
                <span style={activeLanguage === "EN" ? {color: "#FFF", borderBottom: "2px solid #FCB90B"} : {}} onClick={() => setActiveLanguage("EN")}></span>
                <span style={activeLanguage === "SP" ? {color: "#FFF", borderBottom: "2px solid #FCB90B"} : {}} onClick={() => setActiveLanguage("SP")}></span>
                <span style={activeLanguage === "BR" ? {color: "#FFF", borderBottom: "2px solid #FCB90B"} : {}} onClick={() => setActiveLanguage("BR")}></span>
            </div>
            <img src={twitch} alt=""/>
        </div>
    )
}

let Match = (props) => {

    let [isOpen, setIsOpen] = useState(false);
    let [watchHover, setWatchHover] = useState(false)
    let [broadcast, setBroadcast] = useState(false);
    let ref_options = React.createRef();
    let handleOption = (b) => {
        // console.table([ref_options.current])
        // console.log(ref_options.current.scrollHeight)
        // let timeout;
        if (b && !broadcast) {
            window.timeout = setTimeout(() => {
                console.log('index=',props.index)
                document.querySelectorAll(`.${styles.options_wrapper}`)[props.index].style.overflow = 'visible';
            }, 500)
            ref_options.current.style.height = `${ref_options.current.scrollHeight}px`;
        }
        else {
            clearTimeout(window.timeout)
            ref_options.current.style.overflow = 'hidden';
            ref_options.current.style.height = `0px`;
            if (broadcast) setTimeout(() => {
                // alert('nice')
                setIsOpen(false)
            }, 500)
        }
    }
    let handleBroadcast = (b) => {
        console.log(ref_options.current.style.height)
        if (b) {
            window.timeout = setTimeout(() => {
                console.log('index=',props.index)
                // document.querySelectorAll(`.${styles.options_wrapper}`)[props.index].style.overflow = 'visible';
            }, 500)
            ref_options.current.style.height = `${650}px`;
        }
        else {
            ref_options.current.style.overflow = 'hidden';
            ref_options.current.style.height = `0px`;
        }
    }
    return (
        <div style={{marginBottom: 12}}>
            <div className={styles.match__one} style={{background: (isOpen || watchHover) ? "#39394a" : ""}}>

                <div className={styles.match__team}
                     style={{background: "linear-gradient(270deg, rgba(47, 47, 63, 0.5) 0%, #4E426E 135.99%)",
                     opacity: isOpen ? 0 : 1}}>
                    <img src={require(`./img/teams/${'left.png'}`)} alt="" className={styles.match__img}/>
                </div>
                <div className={styles.match__team}
                     style={{background: "linear-gradient(270deg, rgba(59, 58, 79, 0.5) 0%, #4E426E 135.99%)",
                         opacity: isOpen ? 1 : 0}}>
                    <img src={require(`./img/teams/${'left.png'}`)} alt="" className={styles.match__img}/>
                </div>
                <img src={bottom_open} className={styles.bottom_open} onClick={(event) => {
                    if (isOpen) {
                        setTimeout(() => {
                            setBroadcast(false)
                        }, 500)
                    }
                    handleOption(!isOpen)
                    setIsOpen(!isOpen)
                }}/>
                {/*<div className={styles.bottom_arrow}>*/}
                {/*    <span className={styles.line_arrow}></span>*/}
                {/*    <span className={styles.line_arrow}></span>*/}
                {/*</div>*/}
                <img src={arrow_down} alt="" className={styles.bottom_arrow}
                     style={{transform: !isOpen ? "translateX(-50%) rotate(0deg)" : " translateX(-50%) rotate(180deg)", opacity: !isOpen ? 1 : 0}}
                     onClick={(event) => {
                         if (isOpen) {
                             setTimeout(() => {
                                 setBroadcast(false)
                             }, 500)
                         }
                         handleOption(!isOpen)
                         setIsOpen(!isOpen)
                     }}
                />
                <img src={props.watch ? watch_green : watch_grey} alt="" className={styles.watch}
                     onMouseEnter={() => setWatchHover(true)}
                     onMouseLeave={() => setWatchHover(false)}
                     onClick={(event) => {
                         if (props.watch) {
                             setBroadcast(true)
                             handleBroadcast(true)
                             setIsOpen(true)
                         }
                     }}
                />
                {/*{*/}
                {/*   <div alt="" className={styles.div_watch} style={{opacity: watchHover ? 1 : 0,*/}
                {/*       zIndex: watchHover ? 1 : -1,*/}
                {/*   }}      onMouseLeave={() => setWatchHover(false)}>*/}
                {/*       <img src={watch_active} alt=''/>*/}
                {/*   </div>*/}
                {/*}*/}
                <div className={styles.watch_mes} style={{background: "#2f2f3f", opacity: watchHover && props.watch ? 1 : 0}} >
                     
                </div>
                <img src={arrow_up} alt="" className={styles.bottom_arrow}
                     style={{transform: isOpen ? "translateX(-50%) rotate(0deg)" : " translateX(-50%) rotate(-180deg)", opacity: isOpen ? 1 : 0}}
                     onClick={(event) => {
                         handleOption(!isOpen)
                         setIsOpen(!isOpen)
                     }}
                />
                <img src={require(`./img/teams/${props.game_logo}`)} alt="" className={styles.mini_game_logo}/>
                <div className={styles.team_name} style={{justifyContent: "flex-end"}}>
                    {props.left_name}
                </div>
                <div  className={styles.logo__team}>
                    <img src={require(`./img/teams/${props.left_logo}`)} alt=""/>
                </div>
                <div className={styles.block__coef} style={{color: isOpen ? props.left_green ? "#43CA49" : "#F14843" : ""}}>
                    <img src={props.left_green ? arrow_green : arrow_red} alt=""className={styles.coef__arrow} style={{left: 18, opacity: isOpen ? 1 : 0}}/>
                    {props.left_coef}
                </div>
                <div className={styles.match__result}> </div>
                <div className={styles.block__coef} style={{color: isOpen ? props.right_green ? "#43CA49" : "#F14843" : ""}}>
                    <img src={props.right_green ? arrow_green : arrow_red} alt=""className={styles.coef__arrow} style={{right: 18, opacity: isOpen ? 1 : 0}}/>
                    {props.right_coef}
                </div>
                <div  className={styles.logo__team}>
                    <img src={require(`./img/teams/${props.right_logo}`)} alt="" />
                </div>
                <div className={styles.team_name} style={{justifyContent: "flex-start"}}>{props.right_name}</div>

            </div>
            <div className={styles.options_wrapper} ref={ref_options} style={{overflow: "hidden"}}>
                {!broadcast && props.options.map((e, index) => {
                    return <Option {...e} isOpen={!isOpen}/>
                })}
                {broadcast && <BroadCast/>}

            </div>
        </div>
    )
}

export {Match};