import React, {useState, useEffect} from "react";
import styles from "./Header.module.css";
import logo from './img/logo.png'
import arrow from './img/arrow.png';
import line1 from './img/line1.png'
import line2 from './img/line3.png'
import line3 from './img/line2.png';


import user from './img/user.png'
import profile_dollar from './img/dollar_profile.png'
import profile_line from './img/line_profile.png'
import profile_vector from './img/profile_vector.png'

import arrow_input from './img/arrow_input.png'

import cross from './img/cross.png'

import dollar from './img/currency/dollar_grey.png';
import euro from './img/currency/euro_grey.png'
import rubble from './img/currency/rubble_grey.png'
import dollar_grey_1 from './img/dollar_grey_1.png';


import qiwi from './img/qiwi.png'
import yandex from './img/yandex.png'
import visa from './img/visa.png'
import mastercard from './img/mastercard.png'

import icon_1 from './img/icon_1.png'
import icon_2 from './img/icon_2.png'
import icon_3 from './img/icon_3.png'

let inputCurrency = [
    {
        name: "8.05",
        photo: euro,
    },
    {
        name: "20.55",
        photo: dollar,
    },
    {
        name: "10.99",
        photo: rubble,
    }
]

let Header = (props) => {
    let [openBurger, setOpenBurger] = useState(false);
    let [openPutBalance, setOpenPutBalance] = useState(false);
    let [selectedCurrency, setSelectedCurrency] = useState(false)
    let [selectedPayment, setSelectedPayment] = useState("QIWI");
    let [purseValue, setPurseValue] = useState("");
    let [isPayed, setIsPayed] = useState(false);
    let [isRefill, setIsRefill] = useState(false);

    let [isFocusSum, setIsFocusSum] = useState(false);
    let [isFocusPurse, setIsFocusPurse] = useState(false)

    let [errorSum, setErrorSum] = useState(false);

    let [openProfile, setOpenProfile] = useState(false)
    let [openCurrency, setOpenCurrency] = useState(false);
    let [openLanguage, setOpenLanguage] = useState(false);
    let [currentCurrency, setCurrentCurrency] = useState(0);
    let [currentLanguage, setCurrentLanguage] = useState("RUS")

    let [inputValue, setInputValue] = useState(0)

    let handleClick = (event) => {
        console.log(event.target.value)
        if (event.target.value === "+ 7 (") {
            setPurseValue("")
            event.target.value = "";
            return;
        }
        if (event.target.value.length === 8 && purseValue.length === 3) {
            setPurseValue('12')
            event.target.value = "+ 7 (12";
            return;
        }
        let text = event.target.value.replace("(", "")
            .replace(")","").replace("+","").replace(/\s/g, "");
        console.log('text = ' + "|" + text + "|")
        if (text.length !== 1) setPurseValue(event.target.value.replace("(", "").replace(")","")
            .replace("+","").replace(/\s/g, "").slice(1))
        else setPurseValue(event.target.value.replace("(", "").replace(")","")
            .replace("+","").replace(/\s/g, ""));


        // setPurseValue(event.target.value.replace("(", "").replace(")","")
        //     .replace("+","").replace(/\s/g, ""));
        // let text = event.target.value.replace("(", "")
        //     .replace(")","").replace("+","").replace(/\s/g, "");
        if (text.length !== 1) text = text.slice(1);
        console.log('upText = |' + text + "|")
        switch (text.length) {
            case 0: {
                event.target.value = "";
                break;
            }
            case 1: {
                event.target.value = `+ 7 (${text.slice(0, 1)}`;
                break;
            }
            case 2: {
                event.target.value = `+ 7 (${text.slice(0, 2)}`;
                break;
            }
            case 3: {
                event.target.value = `+ 7 (${text.slice(0, 3)})`;
                break;
            }
            case 4: {
                event.target.value = `+ 7 (${text.slice(0, 3)}) ${text.slice(3, 4)}`;
                break;
            }
            case 5: {
                event.target.value = `+ 7 (${text.slice(0, 3)}) ${text.slice(3, 5)}`;
                break;
            }
            case 6: {
                event.target.value = `+ 7 (${text.slice(0, 3)}) ${text.slice(3, 6)}`;
                break;
            }
            case 7: {
                event.target.value = `+ 7 (${text.slice(0, 3)}) ${text.slice(3, 6)} ${text.slice(6, 7)}`;
                break;
            }
            case 8: {
                event.target.value = `+ 7 (${text.slice(0, 3)}) ${text.slice(3, 6)} ${text.slice(6, 8)}`;
                break;
            }
            case 9: {
                event.target.value = `+ 7 (${text.slice(0, 3)}) ${text.slice(3, 6)} ${text.slice(6, 8)} ${text.slice(8, 9)}`;
                break;
            }
            case 10: {
                event.target.value = `+ 7 (${text.slice(0, 3)}) ${text.slice(3, 6)} ${text.slice(6, 8)} ${text.slice(8, 10)}`;
                break;
            }
            default: {
                event.target.value = `+ 7 (${text.slice(0, 3)}) ${text.slice(3, 6)} ${text.slice(6, 8)} ${text.slice(8, 10)}`;
                console.log('SHITHI')
            }
        }
    }
    return (
        <header className={styles.header}>
            {/*{openPutBalance ?*/}
            <div className={openPutBalance ? styles.balance__blur : styles.balance__blur__inactive}
                 onClick={(event) => event.target.className === styles.block__select ? "" : setSelectedCurrency(false)}>
                <div className={openPutBalance ? styles.balance__container__active : styles.balance__container__inactive} style={{height: isPayed ? 567 : isRefill ? 700 : 647}}>
                    <div className={styles.balance__title}>{isRefill ? " " : " "}</div>
                    <img src={cross} alt="" className={styles.balance__exit} onClick={() => {
                        setOpenPutBalance(!openPutBalance)
                        setTimeout(() => {
                            setIsPayed(false);
                        }, 300)
                    }}/>
                    <div className={styles.balance__block}>
                        <div className={styles.b_block__title}></div>
                        <div name="" id="" className={styles.block__select} onClick={() => setSelectedCurrency(!selectedCurrency)}
                             style={{border: selectedCurrency ? "1px solid #FFF" : "1px solid #595D6E"}}>
                            <div className={styles.block__header} value='1'>
                                <img src={inputCurrency[inputValue].photo} alt="" className={styles.currency__logo}/>
                                <span className={styles.currency__value}>{inputCurrency[inputValue].name}</span>
                            </div>
                            <div className={styles.block__header}>
                                <img src={arrow_input} alt="" style={{marginRight: 16, transform: selectedCurrency ? "rotate(180deg)" : "rotate(0deg)"}}/>
                            </div>
                        </div>
                        {selectedCurrency &&
                        <div className={styles.block__wrapper}>
                            {inputCurrency.map((e, index) => {

                                return (
                                        <div className={styles.block__option} onClick={() => setInputValue(index)}>
                                            <div className={styles.option__wrapper}>
                                                <img src={e.photo} alt="" className={styles.option__currency}/>
                                            </div>
                                            <span>{e.name}</span>
                                        </div>

                                )
                            })}
                        </div>
                        }
                    </div>
                    <div className={styles.balance__block}>
                        <div className={styles.b_block__title}> </div>
                        <div className={styles.payment__wrapper}>
                            <div className={styles.payment__block}
                            style={{border: selectedPayment === "QIWI" ? "1px solid #FCB90B" : "1px solid #595D6E"}} onClick={() => setSelectedPayment("QIWI")}
                            >
                                <img src={qiwi} alt=""/>
                            </div>
                            <div className={styles.payment__block}
                             style={{border: selectedPayment === "YANDEX" ? "1px solid #FCB90B" : "1px solid #595D6E"}} onClick={() => setSelectedPayment("YANDEX")}
                            >
                                <img src={yandex} alt=""/>
                            </div>
                            <div className={styles.payment__block}
                             style={{border: selectedPayment === "VISA" ? "1px solid #FCB90B" : "1px solid #595D6E"}} onClick={() => setSelectedPayment("VISA")}
                            >
                                <img src={visa} alt=""/>
                                <img src={mastercard} alt="" style={{marginLeft: 5}}/>
                            </div>
                        </div>
                    </div>
                    <div className={styles.balance__block}>
                        <div className={styles.b_block__title} style={{color: isFocusSum ? "#FCB90B" : ""}}></div>
                        <div style={{display: 'flex', justifyContent: 'space-between'}}>
                            <input type="text" className={styles.input} placeholder='5-100$'
                                   onFocus={() => setIsFocusSum(true)}
                                   style={{border: errorSum ? "1px solid #F14843" : ""}}
                                   onChange={(event) => {
                                       // console.log("123456789".indexOf(event.target.value[event.target.value.length - 1]))
                                       if ("123456789".indexOf(event.target.value[event.target.value.length - 1]) === -1) {
                                           event.target.value = event.target.value.slice(0, event.target.value.length - 1)
                                       }
                                   }}
                                   onBlur={(event) => {
                                       if (Number(event.target.value) < 10) {
                                           setErrorSum(true);
                                       } else setErrorSum(false);
                                       console.log('SJDKFSJKDLFkl')
                                       setIsFocusSum(false)
                                   }}
                            />
                            <div className={styles.input__button} style={{background: errorSum ? "#F14843" : ""}}>
                                <div className={styles.error}
                                     style={{opacity: errorSum ? "1" : "0"}}> 
                                </div>
                                <span className={styles.input__line}></span>
                                <span className={styles.input__line}></span>
                            </div>
                        </div>
                        {isRefill &&
                            <div className={styles.wrapper__calc}>
                                <div className={styles.calc__list}>
                                    <span></span>
                                    <span className={styles.calc__value}>
                                        <span>0</span>
                                        <img src={dollar_grey_1} alt="" style={{marginLeft: 10}}/>
                                    </span>
                                </div>
                                <div className={styles.calc__list}>
                                    <span> </span>
                                    <span className={styles.calc__value}>
                                        <span>0</span>
                                        <img src={dollar_grey_1} alt="" style={{marginLeft: 10}}/>
                                    </span>
                                </div>
                            </div>
                        }
                    </div>
                    <div className={styles.balance__block} style={{display: !isPayed ? "block" : "none"}}>
                        <div className={styles.b_block__title} style={{color: isFocusPurse ? "#FCB90B" : ""}}> </div>
                        <input type="text" className={styles.input} placeholder='   ' style={{width: '100%', textAlign: 'center'}}
                               onFocus={() => setIsFocusPurse(true)}
                               onBlur={() => setIsFocusPurse(false)}
                               onChange={(event) => handleClick(event)}/>
                    </div>
                    <div className={styles.payment__button} onClick={() => isRefill ? "" : setIsPayed(true)}> </div>
                    <div className={styles.payed__message} style={{display: isPayed ? "flex" : "none"}}>
                               
                    </div>
                </div>
            </div>
            {/* :   <div className={styles.balance__blur__inactive}>*/}
            {/*        <div className={styles.balance__container__inactive}>*/}
            {/*            <div className={styles.balance__title}> </div>*/}
            {/*            <img src={cross} alt="" className={styles.balance__exit} onClick={() => setOpenPutBalance(!openPutBalance)}/>*/}
            {/*            <div className={styles.balance__block}>*/}
            {/*                <div className={styles.b_block__title}></div>*/}
            {/*            </div>*/}
            {/*        </div>*/}
            {/*    </div>}*/}
            <div className={styles.header__sidebar}>
                <div className={styles.sidebar__topButton}>
                    <img src={line1} alt="" className={styles.sidebar__line}/>
                    <img src={line2} alt="" className={styles.sidebar__line}/>
                    <img src={line3} alt="" className={styles.sidebar__line}/>
                </div>
                <div className={styles.sidebar__icons__wrapper}>
                    <div className={styles.sidebar__icon}>
                        <img src={icon_1} alt=""/>
                    </div>
                    <div className={styles.sidebar__icon}>
                        <img src={icon_2} alt=""/>
                        <div className={styles.icon__line}></div>
                    </div>
                    <div className={styles.sidebar__icon}>
                        <img src={icon_3} alt=""/>
                    </div>
                </div>
            </div>
            <div className={styles.burger__container} style={{background: openBurger ? 'rgba(37, 38, 50, 0.9)' : 'rgba(37, 38, 50, 0)',
                visibility: openBurger ? "visible" : "hidden"}}>
                <div className={styles.burger__wrapper} style={{opacity: openBurger ? 1 : 0}}>
                    <div className={styles.burger__item}> </div>
                    <div className={styles.burger__item} > </div>
                    <div className={styles.burger__item}></div>
                    <div className={styles.burger__item}></div>
                    <div className={styles.burger__item}>
                        <div className={styles.burger__item_1}>
                            <div className={styles.header__language}>
                        <span className={styles.language__value}>
                            Ru
                        </span>
                                <img src={arrow} className={styles.arrow} alt=""/>
                            </div>
                            <div className={styles.header__currency}>
                                <img src={dollar} className={styles.currency__valute} alt=""/>
                                <img src={arrow} className={styles.arrow} alt=""/>
                            </div>
                        </div>
                    </div>
                    <div className={styles.burger__item_2}>
                        <div className={styles.header__login}>
                            
                        </div>
                        <div className={styles.header__button}>
                            
                        </div>
                    </div>
                </div>
            </div>
            <img src={logo} alt="" className={styles.header__logo} style={props.location.pathname === "/AllBets" ? {marginRight: 100} : {}}/>
            <div className={styles.header__container}>
                <div className={styles.header__list}>
                    <div className={styles.list__item} style={props.location.pathname === "/AllBets" ? {color: "#FFF"} : {}}>
                         
                        { props.location.pathname === "/AllBets" &&
                            <div className={styles.list__bottom__line}/>
                        }
                    </div>
                    <div className={styles.list__item}>
                         

                    </div>
                    <div className={styles.list__item}>
                        
                    </div>
                    <div className={styles.list__item}>
                        
                    </div>

                </div>
                <div className={styles.header__right}>
                    <div className={styles.header__language} onClick={() => setOpenLanguage(!openLanguage)} style={openLanguage ? {border: "1px solid #FFF"} : {}}>
                        <span className={styles.language__value}>
                            {currentLanguage}
                        </span>
                        <div className={styles.language__modal} style={{opacity: openLanguage ? 1 : 0, display: openLanguage ? "block" : "none"}}>
                            <div className={styles.language__item} onClick={() => setCurrentLanguage('ENG')}>
                                ENG
                            </div>
                            <div className={styles.language__item} onClick={() => setCurrentLanguage('RUS')}>
                                RUS
                            </div>
                            <div className={styles.language__item} onClick={() => setCurrentLanguage('FRA')}>
                                FRA
                            </div>
                        </div>
                        <img src={arrow} className={styles.arrow} alt="" style={openLanguage ? {
                            transform: "rotate(180deg)",
                            filter: 'invert(100%) sepia(100%) saturate(0%) hue-rotate(346deg) brightness(104%) contrast(102%)'

                        }:{}}/>
                    </div>
                    <div className={styles.header__currency} onClick={() => setOpenCurrency(!openCurrency)} style={openCurrency ? {border: '1px solid #FFF'} : {}}>
                        {currentCurrency === 0 &&  <img src={euro} className={styles.currency__valute} alt=""/>}
                        {currentCurrency === 1 &&  <img src={rubble} className={styles.currency__valute} alt=""/>}
                        {currentCurrency === 2 &&  <img src={dollar} className={styles.currency__valute} alt=""/>}
                        <img src={arrow} className={styles.arrow} alt="" style={openCurrency ? {
                            transform: "rotate(180deg)",
                            filter: 'invert(100%) sepia(100%) saturate(0%) hue-rotate(346deg) brightness(104%) contrast(102%)'
                        } : {}}/>
                        <div className={styles.currency__window} style={{opacity: openCurrency ? 1 : 0, display: openCurrency ? "block" : "none"}}>
                            <div className={styles.currency__item} onClick={() => setCurrentCurrency(0)}><img src={euro} alt=""/></div>
                            <div className={styles.currency__item} onClick={() => setCurrentCurrency(1)}><img src={rubble} alt=""/></div>
                            <div className={styles.currency__item} onClick={() => setCurrentCurrency(2)}><img src={dollar} alt=""/></div>
                        </div>
                    </div>
                    {props.location.pathname !== "/AllBets" &&
                        <div className={styles.header__login}>
                            
                        </div>
                    }
                    {props.location.pathname !== "/AllBets" &&
                        <div className={styles.header__button}>
                            
                        </div>
                    }
                    {
                        props.location.pathname === "/AllBets" &&
                            <div className={styles.button__replenish} onClick={() => {
                                setIsRefill(false);
                                setOpenPutBalance(!openPutBalance)
                            }}>
                                
                            </div>
                    }
                    {
                        props.location.pathname === "/AllBets" &&
                            <div className={styles.profile} onClick={() => setOpenProfile(!openProfile)}>
                                <img src={user} alt="" className={styles.user}/>
                                <span><img src={profile_dollar} alt="" className={styles.profile__valute}/></span>
                                <span style={{marginLeft: 65}}>222, 22</span>
                                <img src={profile_line} alt="" className={styles.profile__line}/>
                            </div>
                    }
                </div>
            </div>
            <div className={styles.header__modal} style={{opacity: openProfile ? 1 : 0, display: openProfile ? "block" : "none"}}>
                <img src={profile_vector} alt="" className={styles.modal__vector}/>
                <div className={styles.modal__1}>
                    <div className={styles.modal__button} onClick={() => {
                        setIsRefill(true);
                        setOpenPutBalance(!openPutBalance)
                    }}>
                         
                    </div>
                </div>
                <div className={styles.modal__items}>
                    <div className={styles.modal__item}>  </div>
                    <div className={styles.modal__item}> </div>
                    <div className={styles.modal__item}>FAQ</div>
                    <div className={styles.item__delete}>  </div>
                </div>
            </div>
            <div className={openBurger ? styles.burger_menu_active : styles.burger__menu} onClick={() => setOpenBurger(!openBurger)}
                 style={{position: openBurger ? "fixed": "", }}>
                <span></span>
                <span className={styles.middle_span_menu}></span>
                <span></span>
            </div>
        </header>
    )
}

export {Header};