import React from 'react';
import styles from './AllBets.module.css';

import {BetsSidebar} from "../BetsSidebar/BetsSidebar";
import {Slider} from "./parts/Slider";

import {CurrentMatch} from "./parts/CurrentMatch";
import {CompletedMatch} from './parts/CompletedMatch';
import {Inventory} from './parts/Inventory'
import {Footer} from "../Footer/Footer";

let AllBets = (props) => {
    return (

        <div className={styles.wrapper}>
            <BetsSidebar/>
            <div className={styles.bets_main}>
                <div className={styles.bets__content}>
                    <Slider/>
                    <CurrentMatch/>
                    <CompletedMatch/>
                </div>
            </div>
            <div>
                <Inventory/>
            </div>
            <Footer/>
        </div>
    )
}

export {AllBets};