import React, {useState} from 'react';
import styles from './Slider.module.css';

let path_image = [
    "slider1.jpg",
    "slider2.jpg",
    "slider3.jpg",
    "slider4.jpg",
    "slider6.png"
]

let Slider = () => {
    let [pathIndex, setPathIndex] = useState(3);
    return (
        <div className={styles.slider__wrapper}>
            {path_image.map((e, index) => {
                return <img src={require(`./img/${e}`)}
                            style={{opacity: pathIndex === index ? 0.7 : ""}}
                            alt="" className={styles.slider__img}/>
            })}
            <div className={styles.button}>
                 
            </div>
            <div className={styles.block__circle}>
                {path_image.map((e, index) => {
                    console.log('index:',index)
                    return (
                        <div className={styles.circle} style={{opacity: pathIndex === index ? 1 : 0.3}}
                             onClick={() => {
                                 setPathIndex(index)
                             }}>
                        </div>
                    )
                })}
            </div>
        </div>
    )
}

export {Slider}