import React from 'react';
import styles from "./match.module.css";

import completed_match from './img/completed_match.png'
import search from "./img/search.png";

let matches = [
    {
        message: null,
        left_win: true,
        right_win: false,
        logo_left: "pro100.png",
        logo_right: "windigo.png",
        left_team: "PRO100",
        right_team: "Windigo",
        time: "18:00",
        date: "30 .",
        left_points: "12",
        right_points: "11",
    },
    {
        message: null,
        left_win: false,
        right_win: true,
        logo_left: "pro100.png",
        logo_right: "windigo.png",
        left_team: "PRO100",
        right_team: "Windigo",
        time: "14:00",
        date: "29 .",
        left_points: "12",
        right_points: "16",
    },
    {
        message: "",
        left_win: false,
        right_win: true,
        logo_left: "pro100.png",
        logo_right: "windigo.png",
        left_team: "PRO100",
        right_team: "Windigo",
        time: "16:00",
        date: "31 .",
        left_points: "12",
        right_points: "11",
    },
    {
        message: null,
        left_win: true,
        right_win: false,
        logo_left: "pro100.png",
        logo_right: "windigo.png",
        left_team: "PRO100",
        right_team: "Windigo",
        time: "20:00",
        date: "30 .",
        left_points: "12",
        right_points: "11",
    },
]


let Match = (props) => {
    return (
        <div className={styles.match__one} style={{marginBottom: 12}}>

            <div className={styles.match__team}
                 style={{background: "linear-gradient(270deg, rgba(47, 47, 63, 0.5) 0%, #4E426E 135.99%)",
                     opacity: 1}}>
                <img src={require(`./img/teams/${'left.png'}`)} alt="" className={styles.match__img}/>
            </div>
            <img src={require(`./img/teams/cs_grey.png`)} alt="" className={styles.mini_game_logo}/>
            <div className={styles.team_name} style={{justifyContent: "flex-end"}}>
                {props.left_team}
            </div>
            <div  className={styles.logo__team} style={{
                borderRadius: 5,
                border:`2px solid ${props.message ? "#2f2f3f" : props.right_win ? "#399241" : "#A93B3D"}`
            }}>
                <img src={require(`./img/teams/${props.logo_left}`)} alt=""/>
            </div>
            <div className={styles.block__winLose} style={{color: props.left_win ? "#399241" : "#A93B3D"}}>
                {!props.message ? props.left_win ? "Win" : "Lose" : ""}
            </div>
            {!props.message ?
                <div className={styles.match__result} style={{
                    fontWeight: 'bold',
                    fontSize: 24,
                    // lineHeight: 27,
                    // height: "100%",
                    zIndex: 10,
                }}>
                    <span style={{color: props.left_win ? "#FFF" : "#71768B"}}>{props.left_points}</span>
                    &nbsp;:&nbsp;
                    <span style={{color: props.right_win ? "#FFF" : "#71768B"}}>{props.right_points}</span>
                </div>
                 :
                 <div className={styles.match__result} style={{
                    fontStyle: 'normal',
                    fontWeight: 'normal',
                    fontSize: 18,
                    // lineHeight: 21,
                    color: "#71768B",
                    width: 212,
                    textTransform: "uppercase"
                }}>
                    {props.message}
                </div>
            }
            <div className={styles.block__winLose} style={{color: props.right_win ? "#399241" : "#A93B3D"}}>
                {!props.message ? props.right_win ? "Win" : "Lose" : ""}
            </div>
            <div  className={styles.logo__team}  style={{
                borderRadius: 5,
                border:`2px solid ${props.message ? "#2f2f3f" : props.right_win ? "#399241" : "#A93B3D"}`
            }}>
                <img src={require(`./img/teams/${props.logo_right}`)} alt=""/>
            </div>
            <div className={styles.team_name} style={{justifyContent: "flex-start"}}>
                {props.right_team}
            </div>
            <div className={styles.completed_date}>
                <div>{props.time}</div>
                <div style={{fontSize: 12}}>{props.date}</div>
            </div>
            {/*<div className={styles.match__team}*/}
            {/*     style={{background: "linear-gradient(270deg, rgba(59, 58, 79, 0.5) 0%, #4E426E 135.99%)",*/}
            {/*         opacity: isOpen ? 1 : 0}}>*/}
            {/*    <img src={require(`./img/teams/${'1.png'}`)} alt="" className={styles.match__img}/>*/}
            {/*</div>*/}
        </div>
    )
}

let CompletedMatch = () => {
    return (
        <div className={styles.match__wrapper}>
            <div className={styles.match__block__title}>
                <div className={styles.title__left}>
                    <img src={completed_match} alt=""/>
                    <span className={styles.title__value}> </span>
                </div>
                <div className={styles.input__wrapper}>
                    <img src={search} alt="" className={styles.title__search}/>
                    <input type="text" className={styles.title__input} placeholder=" "/>
                </div>
            </div>
            <div className={styles.match__container}>
                {
                    matches.map(e => {
                        return <Match {...e}/>
                    })
                }
            </div>
        </div>
    )
}

export {CompletedMatch}